body {
    margin: 0;
}

html {
    scroll-behavior: smooth;
}

.App {
    display: flex;
    flex-direction: column;
    color: rgb(37, 37, 37);
}

.section {
    margin-bottom: 35px;
    min-height: 100vh;
    padding: 0 15%;
    text-align: center;
}

#navbar {
    background-color: rgb(37, 37, 37);
    color: rgb(255, 255, 255);
    position: fixed;
    top: 0;
    margin: 0;
    height: 40px;
    width: 100%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

#navbar a {
    color: rgb(255, 255, 255);
    margin-right: 15px;
}

a {
    text-decoration: none;
}

#about {
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: center;
    min-height: 100vh;
}

.about__colums{
    width: 30%;
    margin: 0 5%;
    min-height: 240px;
}

#about__info {
    font-size: 1.3em;
    display: flex;
    flex-direction: column;
    text-align: justify;
    text-justify: inter-word;
}

#about__info h1 {
    font-size: 3em;
    margin-top: 0;
}

#skills h1,
#projects h1,
#contact h1 {
    font-size: 3em;
    margin-top: 40px;
}

.skill-item {
    background-color: rgb(37, 37, 37);
    color: white;
    width: 20vw;
    border-radius: 15px;
    padding: 5px 0;
}

#skills-table {
    display: grid;
    padding-top: 15px;
    grid-template-columns: repeat(auto-fit, minmax(250px, 350px));
    justify-content: space-evenly;
}

.skill-item__table {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(75px, 150px));
}

.skill-item__table img {
    height: 48px;
    width: 48px;
}

.project-item{
    background-color: white;
    border-radius: 15px;
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(20%, 50%));
    padding: 10px 0;
    margin: 15px 0;
}

.project-item img {
    width: 80%;
}

.project-item h2 {
    font-size: 2.5em;
    margin: 0;
    text-align: start;
}

.project-item__info {
    text-align: left;
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
}

.project-item__info a{
    margin-right: 10px;
    text-decoration: none;
    padding: 6px 12px;
    border-radius: 20px;
}

.demo-link {
    border: rgb(72, 189, 154) solid 3px;
    color: rgb(72, 189, 154);
    transition: 0.3s;
}

.demo-link:hover {
    border: rgb(72, 189, 154) solid 3px;
    background-color: rgb(72, 189, 154);
    color: rgb(255, 255, 255);
}

.repo-link {
    border: rgb(115, 62, 175) solid 3px;
    color: rgb(115, 62, 175);
    transition: 0.3s;
}

.repo-link:hover {
    border: rgb(115, 62, 175) solid 3px;
    background-color: rgb(115, 62, 175);
    color: rgb(255, 255, 255);
}

.project-item__stack {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-wrap: wrap;
}

.project-item__stack img{
    width: 18px;
    height: 18px;
    margin: 0 3px;
}

#contact {
    margin-bottom: 0;
    display: flex;
    flex-direction: column;
    align-items: center;
}

#contact-info {
    background-color: rgb(37, 37, 37);
    border-radius: 25px;
    color: rgb(255, 255, 255);
    padding: 6% 0;
    width: 50%;
}

#contact__links {
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    min-height: 70px;
}

#contact__links img{
    transition: 0.3s;
}

#contact__links img:hover {
    height: 100%;
    margin: 0 8% 10% 8%;
}

#contact__email {
    display: flex;
    align-items: center;
    justify-content: center;
}

#contact__email h2, 
#contact__email img {
    margin: 0 3%;    
}

.tool {
    display: flex;
    justify-content: center;
    align-items: center;
    color: rgb(42, 83, 71);
    background-color: rgb(103, 202, 172);
    margin-left: 4px;
    border-radius: 15px;
    padding: 0 5px;
}

.tool p {
    margin: 0;
}

.tool img {
    filter: grayscale(100%);
}
